html,body{margin:0;}
*{box-sizing: border-box;}
label,a,span{display:inline-block;}
ul,li{padding:0; margin:0; list-style-type: none;}
h1,h2,h3,figure,p{margin:0;}
main{width:80%; margin:5% auto;}
.btn_parent{text-align:center;}
[type='submit'],[type='button']{
  padding:10px 40px;
  background:#000;
  color:#fff;
  border:none;
}
h1{
  margin:60px 0; font-size:2em; font-weight: normal;
  text-align: center;
}

.add{}
.add ul{}
.add ul li{padding:14px 20px; border-top:1px dashed #ddd;}
.add ul li label{width:10%; vertical-align: middle; color:#777; font-size:12px; font-weight: bold;}
.add ul li input:not([type='submit']){
  width:90%; padding:10px; background:#f3f3f3; border:1px solid #ddd;
  vertical-align: middle;
}
.add ul li textarea{
  width:90%; height:10vw;
  background:#f3f3f3; border:1px solid #ddd;
  vertical-align: middle;
}


table{
  border-collapse: collapse;
  width:100%;
}
table th{background:#333; color:#fff; padding:12px 0; border-top:1px solid #ddd; border-bottom:1px solid #ddd; font-size:14px;}
table td{padding:20px 0; border-bottom:1px dashed #ccc; text-align:center; }
table td:nth-child(1){width:7%;}
table td:nth-child(2){ width:20%; text-align: left;}
table td:nth-child(3){ width:20%;}
table td:nth-child(4){}
table td:nth-child(5){width:15%;}
table tbody tr:nth-of-type(even){
  background:#f5f5f5;
}
.false{display:none}
.true{display:table-row;}

.list{
  display:flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.list figure{
  width:22%; margin:4% 0 0 0;
  background:#fff;
  border:1px solid #ddd;
  padding:3%;
  text-align:center;
}
.list figure figcaption p{margin:30px 0; }
.list figure img{width:100%;}

body{background:#f3f3f3;}